import React, { Component } from 'react';
import {Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton} from 'react-mdl';


class ReactjsProject extends Component {

    //constructor
    constructor(props){
        super(props)
        this.state = {activeTab : 0}
    }

  
    render() { 
        return (
 
            <div className="projects-grid">
                {/* Project 1*/}
                <Card shadow={5} style={{minWidth:'450', margin: 'auto'}}>
                    <CardTitle style={{color: 'white', height: '176px', background:
                    'url(https://reactjs.org/logo-og.png) center / cover'}}>
                        React Projects #1
                    </CardTitle>
                    <CardText>
                        Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. 
                    </CardText>
                    
                    <CardActions border>
                        <Button src="https://github.com/VanessaMachi/OOP_Study_Python" colored>GitHub</Button>
                        <Button colored disabled>CodePen</Button>
                        <Button colored disabled>LiveDemo</Button>
                    </CardActions>

                    <CardMenu style={{color: '#fff'}}>
                        <IconButton name="shared" />
                    </CardMenu>
                </Card>
            </div>
        );
    }
}
 
export default ReactjsProject;
