import React, { Component } from 'react';
import {Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton, Redirect} from 'react-mdl';


class ProjectsPython extends Component {

    //constructor
    constructor(props){
        super(props)
        this.state = {activeTab : 0}
        this.projectname = "Python"
    }

  
    render() { 
        return (
 
            <div className="projects-grid">
                {/* Project 1*/}
                <Card shadow={5} style={{minWidth:'450', margin: 'auto'}}>
                    <CardTitle style={{color: 'black', height: '176px', background:
                    'url(https://softwarelogy.com/wp-content/uploads/2018/12/Untitled-3.png) center / cover'}}>
                        Python Projects 1
                    </CardTitle>
                    <CardText>
                    (Self Study Purpose) This project is for my self study and perform the concept of OOP in coding. 
                    The code I shared in github is present the basic concept of class instance, class variable, regular method and static method.
                    </CardText>
                    
                    <CardActions border>
                        <Button href="https://github.com/VanessaMachi/OOP_Study_Python" target="_blank" colored>GitHub</Button>
                        <Button colored disabled>CodePen</Button>
                        <Button colored disabled>LiveDemo</Button>
                    </CardActions>

                    <CardMenu style={{color: '#fff'}}>
                        <IconButton name="shared" />
                    </CardMenu>
                </Card>
                {/* Project 2*/}
                <Card shadow={5} style={{minWidth:'450', margin: 'auto'}}>
                    <CardTitle style={{color: 'black', height: '176px', background:
                    'url(https://softwarelogy.com/wp-content/uploads/2018/12/Untitled-3.png) center / cover'}}>
                        Python Projects 2
                    </CardTitle>
                    <CardText>
                    (Self Study Purpose) Algorithm study in tree traversal. Tree traversal is a systematic way of visiting node in a tree, in the github link I share is showing my work of 
                    three element of depth first, pre-order tree traversal, in-order tree traversal and post-order traversal.
                    </CardText>
                    
                    <CardActions border>
                        <Button colored>GitHub</Button>
                        <Button colored disabled>CodePen</Button>
                        <Button colored disabled>LiveDemo</Button>
                    </CardActions>

                    <CardMenu style={{color: '#fff'}}>
                        <IconButton name="shared" />
                    </CardMenu>
                </Card>
              
            </div>
        );
    }
}
 
export default ProjectsPython;
